const yer = date.getFullYear();

function dateFun(value) {	
	let d = value.day;
	let m = value.month;
	let year = value.year;

	if(d < 10) d = "0"+d;
	if(m < 10) m = "0"+m;

	return d + "." + m + "." + year;
}

function WorkSelect(v) {
	return v.toString().replace(/10/gi, "Физика").replace(/11/gi, "География").replace(/12/gi, "Биология")
	.replace(/13/gi, "Информатика").replace(/1/gi, "Алгебра").replace(/2/gi, "Геометрия").replace(/3/gi, "Русский язык")
			.replace(/4/gi, "Английский язык").replace(/5/gi, "Немецкий язык").replace(/6/gi, "Литература")
			.replace(/7/gi, "История").replace(/8/gi, "Обществознание").replace(/9/gi, "Химия")
			;
}

function schoolWorkSelect(v) {
	return v.toString().replace(/10/gi, "physics").replace(/11/gi, "geography").replace(/12/gi, "biology")
	.replace(/13/gi, "informatics").replace(/1/gi, "algebra").replace(/2/gi, "geometry").replace(/3/gi, "russian")
			.replace(/4/gi, "english").replace(/5/gi, "german").replace(/6/gi, "literature")
			.replace(/7/gi, "history").replace(/8/gi, "social").replace(/9/gi, "chemistry")
			;
}

